        </p>
      </div>
    </div>
    <div class="about-information">
      <div class="row-1">
        <div class="row-1-img">
          <img src="@/assets/imgs/about-3.jpg" alt="" />
        </div>
        <div class="row-1-text row-text">
          <p></p>
          <p>{{ studentNumber }}</p>
        </div>
      </div>
      <div class="row-2">
        <div class="row-2-img">
          <img src="@/assets/imgs/about-3.jpg" alt="" />
        </div>
        <div class="row-2-text row-text">
          <p></p>
          <p>{{ bodyNumber }}% </p>
        </div>
        <div class="row-2-img">
          <img src="@/assets/imgs/about-3.jpg" alt="" />
        </div>
      </div>
      <div class="row-3">
        <div class="row-3-text row-text">
          <p>!</p>
        </div>
        <div class="row-3-img">
          <img src="@/assets/imgs/about-3.jpg" alt="" />
        </div>
      </div>
    </div>

    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '@/components/Footer.vue';
import { gsap } from 'gsap/all';

export default {
  name: 'About',
  components: {
    Footer,
  },
  data() {
    return {
      action: false,
      studentTweenedNumber: 0,
      bodyTweenedNumber: 0,
    };
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      if (window.scrollY >= 700) {
        gsap.to(this.$data, { duration: 1, studentTweenedNumber: 850 });
      }
      if (window.scrollY >= 1030) {
        gsap.to(this.$data, { duration: 1, bodyTweenedNumber: 2032.5 });
      }
      console.log(window.scrollY);
    },
  },
  computed: {
    studentNumber: function () {
      return this.studentTweenedNumber.toFixed(0);
    },
    bodyNumber: function () {
      return this.bodyTweenedNumber.toFixed(1);
    },
  },

  mounted() {
    setTimeout(() => {
      this.$store.commit('LOADING', false);
    }, 1500);
    setTimeout(() => {
      this.action = true;
    }, 1600);
  },
};
</script>
